    &lt;/mku-swiper-item&gt;
    &lt;mku-swiper-item&gt;
      &lt;div class=&quot;doc-swiper__item&quot;&gt;2&lt;/div&gt;
    &lt;/mku-swiper-item&gt;
    &lt;mku-swiper-item&gt;
      &lt;div class=&quot;doc-swiper__item&quot;&gt;3&lt;/div&gt;
    &lt;/mku-swiper-item&gt;
  &lt;/mku-swiper&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>模拟异步获取数据</h3>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-button class=&quot;doc-mb20&quot; @click=&quot;getList&quot;&gt;2s后展示轮播数据&lt;/mku-button&gt;
  &lt;mku-swiper :value=&quot;2&quot;&gt;
    &lt;mku-swiper-item v-for=&quot;item in list&quot;&gt;
      &lt;div class=&quot;doc-swiper__item&quot;&gt;{{item}}&lt;/div&gt;
    &lt;/mku-swiper-item&gt;
  &lt;/mku-swiper&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      list: []
    }
  },
  methods: {
