<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>0</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">width</td>
<td style="text-align:left">指定宽度</td>
<td style="text-align:center"><code>String | Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>100%</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">height</td>
<td style="text-align:left">指定高度</td>
<td style="text-align:center"><code>String | Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>auto</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">loop</td>
<td style="text-align:left">是否循环轮播</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>true</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">autoplay</td>
<td style="text-align:left">自动轮播</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>true</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">duration</td>
<td style="text-align:left">自动轮播时间周期，单位<code>ms</code></td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>3000</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">disable-on-interaction</td>
<td style="text-align:left">用户操作后停止autoplay，不会恢复。例如鼠标滑入</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">speed</td>
<td style="text-align:left">轮播动画执行速度，单位<code>ms</code></td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>300</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">arrow</td>
<td style="text-align:left">左右切换按钮的显示方式，支持滑入时显示、一直显示、隐藏</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>hover | always | never</code></td>
<td style="text-align:center"><code>hover</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">trigger</td>
<td style="text-align:left">分页器触发方式</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>hover | click</code></td>
<td style="text-align:center"><code>click</code></td>
<td style="text-align:center">否</td>
</tr>
</tbody>
</table>
<h3>Swiper Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">轮播图切换时触发</td>
<td style="text-align:left">参数1：当前索引；参数2：上一张索引</td>
</tr>
<tr>
<td style="text-align:left">next-click</td>
<td style="text-align:left">点击<code>next</code>切换按钮或调用<code>prev</code>方法时触发</td>
<td style="text-align:left">参数1：当前索引；参数2：上一张索引</td>
</tr>
<tr>
<td style="text-align:left">prev-click</td>
<td style="text-align:left">点击<code>prev</code>切换按钮或调用<code>next</code>方法时触发</td>
<td style="text-align:left">参数1：当前索引；参数2：上一张索引</td>
</tr>
<tr>
<td style="text-align:left">animate-begin</td>
<td style="text-align:left">动画开始前触发</td>
<td style="text-align:left">当前索引</td>
</tr>
<tr>
<td style="text-align:left">animate-finish</td>
<td style="text-align:left">动画完成后触发</td>
<td style="text-align:left">当前索引</td>
</tr>
</tbody>
</table>
<h3>Swiper Methods</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">swipeTo</td>
<td style="text-align:left">滚动到指定索引处</td>
</tr>
<tr>
<td style="text-align:left">prev</td>
<td style="text-align:left">滚动到上一页</td>
</tr>
<tr>
<td style="text-align:left">next</td>
<td style="text-align:left">滚动到下一页</td>
</tr>
</tbody>
</table>
